import * as React from 'react';
import * as moment from 'moment';

import './CountdownTimer.css';
import {timeNow} from '../../Utils/TimeUtil';

import CountdownTimerItem from './CountdownTimerItem';
import {BBL_DURATION_MINS} from '../../Data/Matches';

export interface Countdown {
    now: moment.Moment;
    days: number;
    hours: number;
    minutes: number;
    minutesString: string;
    seconds: number;
    secondsString: string;
    inProgress: boolean;
    complete: boolean;
}

interface CountdownTimerProps {
    eventTime: moment.Moment;
    showDays?: boolean;
    showHours?: boolean;
    showMinutes?: boolean;
    showSeconds?: boolean;
}

interface CountdownTimerState {
    countdown: Countdown;
}

export default class CountdownTimer extends React.PureComponent <CountdownTimerProps,
CountdownTimerState> {

    public timer = () => setInterval(() => this.tick(), 1000);

    constructor(props: CountdownTimerProps) {
        super(props);
        this.state = {
            countdown: this.generateCountdown(timeNow(), this.props.eventTime, BBL_DURATION_MINS)
        };
    }

    componentDidMount() {
        this.timer();
    }

    render() {
        const countdownTimer = this.renderCountdown();
        return (
            countdownTimer
        );
    }

    private tick() {
        this.setState({
            countdown: this.generateCountdown(timeNow(), this.props.eventTime, BBL_DURATION_MINS)
        });
    }

    private renderCountdown() {
        const countdown = this.state.countdown;
        if (countdown && countdown.inProgress) {
            return this.renderInProgress();
        } else if (countdown && countdown.complete) {
            return this.renderComplete();
        } else if (countdown) {
            return this.renderCounter(countdown);
        } else {
            return undefined;
        }
    }

    private renderCounter(countdown: Countdown) {
        return (
            <div className="feature__countdown_timer">
                <CountdownTimerItem value={countdown.days.toString()} label="days"/>
                <CountdownTimerItem value={countdown.hours.toString()} label="hours"/>
                <CountdownTimerItem value={countdown.minutesString} label="minutes"/>
                <CountdownTimerItem value={countdown.secondsString} label="seconds"/>
            </div>
        );
    }

    private renderInProgress() {
        return (
            <div className="feature__countdown_timer">
                Game in progress
            </div>
        );
    }

    private renderComplete() {
        return (
            <div className="feature__countdown_timer">
                Game complete
            </div>
        );
    }

    private generateCountdown(countdownFrom: moment.Moment, startTime: moment.Moment, eventDuration: number): Countdown {
        const now = countdownFrom;
        const endTime = startTime
            .clone()
            .add(eventDuration, 'minutes');

        let countdown: Countdown = {
            now,
            days: 0,
            hours: 0,
            minutes: 0,
            minutesString: '',
            seconds: 0,
            secondsString: '',
            complete: false,
            inProgress: false
        };
        if (startTime.isBefore(now) && endTime.isAfter(now)) { // event is in progress
            countdown.inProgress = true;
        } else if (endTime.isBefore(now)) { // event has finished
            countdown.complete = true;
        } else { // event has not happened yet
            const days: number = startTime.diff(now, 'days');

            const hours: number = days > 0 ?
                Math.floor(startTime.diff(now, 'hours') % 24) :
                startTime.diff(now, 'hours'); // cant divide by 0

            const minutes: number = hours > 0 ?
                Math.floor(startTime.diff(now, 'minutes') % 60) :
                startTime.diff(now, 'minutes');
            const minutesString: string = ('0' + minutes).substr(-2);

            const seconds: number = minutes > 0 ?
                Math.floor(startTime.diff(now, 'seconds') % 60) :
                startTime.diff(now, 'seconds');
            const secondsString: string = ('0' + seconds).substr(-2);

            countdown = {
                ...countdown,
                days,
                hours,
                minutes,
                minutesString,
                seconds,
                secondsString
            };
        }
        return countdown;
    }
}



