import * as React from 'react';
import * as moment from 'moment';

import CountdownTimer from '../CoundownTimer/CountdownTimer';

import {Match} from '../../Data/Matches';
import {
    UI_DATE_FORMAT,
    UI_TIME_FORMAT
} from '../../Utils/DateUtil';

import './Feature.css';

export interface FeatureProps {
    match : Match;
}

export default class Feature extends React.PureComponent <FeatureProps> {
    render() {
        const match = this.props.match;
        const time = moment(match.date).local();

        return (
            <div className="feature">
                <div className="feature__teams">
                    <div className="feature__home_team">
                        <img className="feature__team_logo" src={match.homeTeam.imageUrl} alt={match.homeTeam.name}/>
                        <h2 className="feature__team_title">
                            {match.homeTeam.name}
                        </h2>
                    </div>
                    <div className="feature__team_spacer"/>
                    <div className="feature__away_team">
                        <img className="feature__team_logo" src={match.awayTeam.imageUrl} alt={match.awayTeam.name}/>
                        <h2 className="feature__team_title">
                            {match.awayTeam.name}
                        </h2>
                    </div>
                </div>
                <div className="feature__match_details">
                    <div className="feature__item">
                        {`Game ${match.matchNumber}`}<br/>
                        {match.ground.name.toString()}, {match.ground.suburb.toString()}, {match.ground.state.name.toString()}
                    </div>
                    <div className="feature__item large">{time.format(UI_DATE_FORMAT)}</div>
                    <div className="feature__item large">{time.format(UI_TIME_FORMAT)}&nbsp;
                        <span className="span">(Your time)</span>
                    </div>
                    <CountdownTimer
                        eventTime={moment(match.date)}
                    />
                </div>
            </div>
        );
    }
}



